import {Component, OnInit, ViewContainerRef} from '@angular/core';
import {DashboardService} from './dashboard.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import {Observable} from 'rxjs/Rx';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

class teamObject {
    Id?: string;
    TeamName: string;
    Status: string;
    Count?:string;
    TeamId?:string;
}
@Component({
  templateUrl: 'dashboard.component.html'
})
export class DashboardComponent implements OnInit{
	 public teams;
   public  closeResult;
   public teamObject: teamObject = {TeamName:'', Status:'Active'};
  constructor(private _teamService: DashboardService, private spinnerService: Ng4LoadingSpinnerService,
              private modalService: NgbModal, public toastr: ToastsManager, vcr: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {
    this.spinnerService.show();
    this.getAllTeams();
  }
  open(content, type, data) {
      if(type == 'edit'){this.teamObject = data;}
    this.modalService.open(content).result.then((result) => {
      this.closeResult = `${result}`;
      if(this.closeResult == 'yes'){
          if(type == 'create'){this.createTeam(this.teamObject);}
          if(type == 'edit'){this.updateTeam(this.teamObject);}
      }
    }, (reason) => {
      this.closeResult = `Dismissed`;
    });
  }
  getAllTeams() {
        this._teamService.getTeams().subscribe(
      // the first argument is a function which runs on success
      data => { this.teams = data; console.log(this.teams);
        this.spinnerService.hide();
      },
      // the second argument is a function which runs on error
      err => {
        this.toastr.error('Error While loading!', 'Oops!');
        this.spinnerService.hide();
      },
      // the third argument is a function which runs on completion
      () => {console.log('done team loading');
      this.spinnerService.hide();}
    );
  }
  deleteTeam(Id){
    this.spinnerService.show();
    this._teamService.deleteRecord(Id).subscribe(
         data => {
           // refresh the list
           this.getAllTeams();
           this.toastr.success('Team Deleted Successfully!', 'Success!');
           return true;
         },
         error => {
           this.spinnerService.hide();
           this.toastr.error('Error While Deleting! Please Try Again Later', 'Oops!');
           return Observable.throw(error);
         }
      );
    }
  updateTeam(entity) {
    this.spinnerService.show();
    let newEntity = entity;
    this._teamService.updateRecord(newEntity).subscribe(
       data => {
         // refresh the list
         this.getAllTeams();
         this.toastr.success('Team Updated Successfully!', 'Success!');
         return true;
       },
       error => {
         this.spinnerService.hide();
         this.toastr.error('Error While Saving! Please Try Again Later', 'Oops!');
         return Observable.throw(error);
       }
    );
  }
  createTeam(entity) {
    this.spinnerService.show();
    let newEntity = entity;
    this._teamService.insertRecord(newEntity).subscribe(
       data => {
         // refresh the list
         this.getAllTeams();
         this.toastr.success('Team Created Successfully!', 'Success!');
         return true;
       },
       error => {
         this.spinnerService.hide();
         this.toastr.error('Error While Creating! Please Try Again Later', 'Oops!');
         return Observable.throw(error);
       }
    );
  }
}



