import {Injectable} from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Observable} from 'rxjs/Observable';

const httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable()
export class DashboardService {
  public localEnv= 'http://localhost:56170/dist/';

  constructor(private http: HttpClient) {
    }
    // Teams
     getTeams() {
        return this.http.get(this.localEnv + 'api/teams');
    }
    deleteRecord(id) {
        return this.http.delete(this.localEnv + 'api/teams/' + id);
    }
    updateRecord(entity) {
        let body = JSON.stringify(entity);
        return this.http.put(this.localEnv + 'api/teams/' + entity.Id, body, httpOptions);
    }
    insertRecord(entity) {
        let body = JSON.stringify(entity);
        return this.http.post(this.localEnv + 'api/teams/', body, httpOptions);
    }
    // Employess
    getEmployees(teamID){
    	 return this.http.get(this.localEnv + 'api/employees/' + teamID);
    }
    insertEmployee(entity) {
        let body = JSON.stringify(entity);
        return this.http.post(this.localEnv + 'api/employees/', body, httpOptions);
    }
    updateEmployee(entity) {
        let body = JSON.stringify(entity);
        return this.http.put(this.localEnv + 'api/employees/' + entity.Id, body, httpOptions);
    }
  updateEmployeeMany(entity1, entity2) {
  let body = JSON.stringify(entity1);
  return this.http.post(this.localEnv + 'api/employees/' + entity2.Id, body, httpOptions);
}
    deleteEmployee(id) {
        return this.http.delete(this.localEnv + 'api/employees/' + id);
    }
}



