import { Component, OnInit, ViewContainerRef } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import {DashboardService} from './dashboard.service';
import * as moment from 'moment';
import {ActivatedRoute} from '@angular/router';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

class empObject {
    Id?: string;
    Name: string;
    Role?: string;
    Status: string;
    Mobile?:string;
    Email?:string;
    PagerStatus?: string;
    TeamId:string;
    StartDate?:any;
    EndDate?:any;
}
class pagerInfo{
  Prev?: string ;
  Next?: string ;
  Current?: string ;
}
@Component({
  templateUrl: 'team-details.component.html'
})

export class TeamDetailsComponent implements  OnInit{
      public teamName;
      public results;
      public closeResult;
      public pagerNextInfo;
      public pagerInfo: pagerInfo= {};
      public now: any;
      public empObject: empObject = {Name:'', Status:'Active', TeamId:''};
      public nextEmpdata: any;
    constructor( private _teamService: DashboardService, private modalService: NgbModal,
      private route: ActivatedRoute, private spinnerService: Ng4LoadingSpinnerService,
                 public toastr: ToastsManager, vcr: ViewContainerRef ) {
          this.route.params.subscribe( params => this.teamName = params.id );
          this.empObject.TeamId = this.teamName.toString();
          this.now = moment;
      this.toastr.setRootViewContainerRef(vcr);

    }
    ngOnInit() {
      this.spinnerService.show();
    this.getAllMembers();
    }
    open(content, type, data) {
      this.empObject = {Name:'', Status:'Active', TeamId: this.empObject.TeamId}; // model scope clear
            if(type == 'edit'){
              this.empObject = data;
              console.log(data) ;}
          this.modalService.open(content).result.then((result) => {
            this.closeResult = `${result}`;
        if(this.closeResult == 'yes'){
            if(type == 'create'){
              this.createMember(this.empObject);}
            if(type == 'edit'){
              this.updateMember(this.empObject);
              console.log(this.empObject);}
          if(type == 'delete'){
            this.deleteMember(data);
            }
          if(type == 'swap'){
            this.setPager(data);
          }
        }
      }, (reason) => {
        this.closeResult = `Dismissed`;
      });
    }
    Convert(data, flag){
      if(flag){
        let newData = {year:null, month:null,day:null};
        data = data.split("-");
        newData.year = Number(data[0]);newData.month = Number(data[1]);newData.day = Number(data[2]);
        return newData;
      } else {
      return data.year + '-' + data.month + '-' + data.day;
      }
    }
    getAllMembers(){

      this._teamService.getEmployees(this.teamName).subscribe(
      // the first argument is a function which runs on success
      data => {
        this.results = data; this.setPager(false);
        this.spinnerService.hide();
      },
      // the second argument is a function which runs on error
      err => {console.error(err)
        this.spinnerService.hide();
        this.toastr.error('Error While loading the data!!', 'Oops!');
      },
      // the third argument is a function which runs on completion
      () => {
       this.spinnerService.hide(); }
    );
    }
    setPager(flag) {
       const currentDate = this.now().format('YYYY-MM-DD');

      if(flag){
        if(flag.Name === this.pagerInfo.Next || flag.Name === this.pagerInfo.Current ){
          this.toastr.info('You cannot assign pager to this user.', 'Already Assigned!');
          return false;
        }
        let a = this.now(currentDate);
        let b = this.Convert(flag.EndDate, false);
        if ( a.diff(b, 'days') > 0 ) {
          flag.EndDate = {year: 0, month: 0, day: 0 };
          flag.StartDate = {year: 0, month: 0, day: 0 };
        }
         this.updateMany(flag,this.nextEmpdata);
      } else {
        this.pagerInfo = {'Next': '', 'Prev': '', 'Current': ''};
        for (let prop of this.results) {
          const result = this.now(currentDate).isBetween(this.Convert(prop.StartDate, false), this.Convert(prop.EndDate, false));
          const temp = this.now(currentDate).isSame(this.Convert(prop.StartDate, false)) ?
            this.now(currentDate).isSame(this.Convert(prop.StartDate, false))
            : this.now(currentDate).isSame(this.Convert(prop.EndDate, false));
          if(result || temp){
            this.pagerInfo.Current = prop.Name;
            let  f = 0;
            let e = 0;
            for (let iprop of this.results) {

              let a = this.now(this.Convert(prop.StartDate, false));
              let b = this.now(this.Convert(iprop.EndDate, false));
              if((a.diff(b, 'days') >= 0 ) && ( e > a.diff(b, 'days') || e === 0)) {
                e = a.diff(b, 'days');
                this.pagerInfo.Prev = iprop.Name;
              }
              let d = this.now(this.Convert(prop.EndDate, false));
              let c = this.now(this.Convert(iprop.StartDate, false));
             if( (c.diff(d, 'days') >= 0) && (f > c.diff(d, 'days') || f === 0)){
               f = c.diff(d, 'days');
               console.log(f);
               this.pagerInfo.Next = iprop.Name;
               this.nextEmpdata = iprop;
             }
            }
          }
         }}};
    createMember(entity) {
      this.spinnerService.show();
    const newEntity = entity;
    this._teamService.insertEmployee(newEntity).subscribe(
       data => {
         // refresh the list
         this.getAllMembers();
         this.toastr.success('Member Created Successfully!', 'Success!');
         return true;
       },
       error => {
         this.spinnerService.hide();
         this.toastr.error('Error While creating the Member', 'Please Try Again!');
         return Observable.throw(error);
       }
    );
  }
  updateMember(entity) {
    const newEntity = entity;
    this._teamService.updateEmployee(newEntity).subscribe(
       data => {
         // refresh the list
         this.getAllMembers();
           this.toastr.success('Updated Successfully!', 'Success!');
         return true;
       },
       error => {
         this.spinnerService.hide();
         this.toastr.error('Error While updating', 'Please Try Again!');
         return Observable.throw(error);
       }
    );
  }
  updateMany(param1, param2) {
    const Entity1 = param1;
    const Entity2 = param2;
    this._teamService.updateEmployeeMany(Entity1, Entity2).subscribe(
      data => {
        this.getAllMembers();
        this.toastr.success('Updated Successfully!', 'Success!');
        return true;
      },
      error => {
        this.spinnerService.hide();
        this.toastr.error('Error While updating', 'Please Try Again!');
        return Observable.throw(error);
      }
    );
  }
  deleteMember(data){
      if(data.Name === this.pagerInfo.Current){
        this.toastr.info('You cannot delete this user.', 'Pager Active!');
        return false;
      }
    this.spinnerService.show();
    this._teamService.deleteEmployee(data.Id).subscribe(
         data => {
           // refresh the list
           this.getAllMembers();
           this.toastr.success('Member deleted Successfully!', 'Success!');
           return true;
         },
         error => {
           this.spinnerService.hide();
           this.toastr.error('Error While deleting!!', 'Please Try Again!');
           return Observable.throw(error);
         }
      );
    }

}



